import React, { useState } from 'react';
import { motion } from 'framer-motion';
import Button from '../components/Button';
import Modal from '../components/Modal';
import SEO from '../components/SEO';
import Testimonials from '../components/Testimonials';
import { useTheme } from '../contexts/ThemeContext';
import { colors, typography } from '../styles/sharedStyles';

function Home() {
  const { darkMode } = useTheme();
  const [selectedService, setSelectedService] = useState(null);

  const services = [
    {
      title: "Derecho Tecnológico y Fintech",
      icon: "💻",
      description: "Asesoramiento en regulaciones tecnológicas y finanzas digitales.",
      details: "Nuestro equipo especializado en derecho tecnológico y fintech ofrece asesoramiento integral en temas como protección de datos, regulación de criptomonedas, contratos inteligentes y cumplimiento normativo en el sector fintech."
    },
    {
      title: "Derecho Ambiental y Energías Renovables",
      icon: "🌿",
      description: "Soluciones legales para proyectos sostenibles y energías limpias.",
      details: "Brindamos asesoría legal especializada en proyectos de energías renovables, gestión ambiental, permisos y licencias ambientales, así como en la implementación de políticas de sostenibilidad corporativa."
    },
    {
      title: "Propiedad Intelectual en E-commerce",
      icon: "🛡️",
      description: "Protección de marcas y derechos de autor en el comercio electrónico.",
      details: "Ofrecemos servicios de protección de propiedad intelectual en el ámbito digital, incluyendo registro de marcas en línea, protección de derechos de autor en plataformas digitales y resolución de disputas de nombres de dominio."
    },
  ];

  return (
    <>
      <SEO 
        title="Inicio"
        description="Bufete Salas Granados ofrece asesoría legal estratégica para el éxito empresarial en Costa Rica. Especialistas en derecho corporativo, tecnológico y ambiental."
        keywords="abogados costa rica, asesoría legal, derecho corporativo, derecho tecnológico, derecho ambiental"
      />
      <div className={`${colors.background} ${typography.sans} ${darkMode ? 'dark' : ''}`}>
        <section className="relative bg-gradient-to-r from-blue-600 to-blue-800 text-white py-32">
          <div className="absolute inset-0 bg-black opacity-50"></div>
          <div className="container mx-auto px-4 relative z-10">
            <motion.h1 
              className="text-5xl md:text-6xl font-bold mb-6 leading-tight"
              initial={{ opacity: 0, y: -20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              Bufete Salas Granados
            </motion.h1>
            <motion.p 
              className="text-2xl mb-8 max-w-2xl"
              initial={{ opacity: 0, y: -20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.2 }}
            >
              Asesoría legal estratégica para el éxito empresarial en Costa Rica
            </motion.p>
            <Button to="/contacto" variant="secondary" className="text-lg px-8 py-3">Solicitar Consulta</Button>
          </div>
        </section>

        <section className="py-24 bg-gray-100 dark:bg-gray-800">
          <div className="container mx-auto px-4">
            <h2 className="text-4xl font-bold mb-16 text-center">Áreas de Práctica Especializadas</h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-12">
              {services.map((service, index) => (
                <motion.div 
                  key={index}
                  className="bg-white dark:bg-gray-700 rounded-lg shadow-lg p-8 text-center cursor-pointer transform transition duration-300 hover:scale-105"
                  whileHover={{ y: -10 }}
                  onClick={() => setSelectedService(service)}
                >
                  <div className="text-5xl mb-6" aria-hidden="true">{service.icon}</div>
                  <h3 className="text-2xl font-semibold mb-4 dark:text-white">{service.title}</h3>
                  <p className={`${colors.text} dark:text-gray-300`}>{service.description}</p>
                </motion.div>
              ))}
            </div>
          </div>
        </section>

        <section className={`${colors.secondary} text-white py-24`}>
          <div className="container mx-auto px-4">
            <h2 className="text-4xl font-bold mb-12 text-center">Nuestro Enfoque</h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
              {[
                "Análisis legal predictivo con tecnología de vanguardia",
                "Estrategias de resolución alternativa de conflictos",
                "Asesoría en cumplimiento normativo internacional",
                "Enfoque en sostenibilidad legal y responsabilidad social",
              ].map((item, index) => (
                <motion.div 
                  key={index} 
                  className="flex items-center bg-white dark:bg-gray-700 text-gray-800 dark:text-white rounded-lg shadow-lg p-6"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: index * 0.1 }}
                >
                  <span className="text-blue-500 text-3xl mr-4" aria-hidden="true">✔️</span>
                  <span className="text-lg">{item}</span>
                </motion.div>
              ))}
            </div>
          </div>
        </section>

        <Testimonials />

        <section className="py-24 bg-gray-100 dark:bg-gray-800">
          <div className="container mx-auto px-4 text-center">
            <h2 className="text-4xl font-bold mb-8 dark:text-white">¿Listo para impulsar su negocio?</h2>
            <p className="text-xl mb-12 max-w-2xl mx-auto dark:text-gray-300">Agende una consulta estratégica y descubra cómo podemos ayudarle a alcanzar sus objetivos legales y empresariales.</p>
            <Button to="/contacto" variant="primary" className="text-lg px-8 py-3">Agendar Consulta</Button>
          </div>
        </section>

        <Modal
          isOpen={!!selectedService}
          onClose={() => setSelectedService(null)}
          title={selectedService?.title}
        >
          <p className="mb-6 text-gray-700 dark:text-gray-300">{selectedService?.details}</p>
          <div className="mt-4">
            <Button to="/servicios" variant="primary">Ver todos los servicios</Button>
          </div>
        </Modal>
      </div>
    </>
  );
}

export default Home;